body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --azul: #3b82f6;
  --blanco: #ffffff;
  --gris-claro: #f5f5f5;
  --gris: #94a3b8;
  --gris-oscuro: #64748b;
  --negro: #000;
  /* To do List*/
  --beig-clarito: #F9F5EB;
  --beig-gris: #E4DCCF;
  --rojo-claro: #EA5455;
  --azul-marino: rgb(0, 43, 91);
  /* Out */
  --gris-clarito: #6B717E;
  --rosa-fuerte: #EFAAC4;
  --rosa-medio: #FFC4D1;
  --beig-fuerte: #FFE8E1;
  --verde-flojito: #D4DCCD;
}

body {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
  background-color: var(--beig-clarito);
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.fijar {
  overflow: hidden;
  height: 100vh;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

.sombra {
  box-shadow: 0px 9px 10px -10px rgba(0, 0, 0, 0.51);
  -webkit-box-shadow: 0px 9px 10px -10px rgba(0, 0, 0, 0.51);
  -moz-box-shadow: 0px 9px 10px -10px rgba(0, 0, 0, 0.51);
  background-color: var(--beig-gris);
  padding: 4rem;
  border-radius: 1.2rem;
}


.header h1 {
  padding: 3rem 0;
  margin: 0;
  color: var(--azul-marino);
  text-align: center;
  margin-top: 150px;
  text-transform: uppercase;
}

.formulario {
  width: 95%;
  margin: 0 auto;
  padding: 10rem 0;
}

@media (min-width: 768px) {
  .formulario {
    padding: 5rem 0;
    width: 60rem;
  }
}

.formulario legend {
  font-size: 3.6rem;
  text-align: center;
  display: block;
  text-transform: uppercase;
  color: var(--azul-marino);
  margin-bottom: 4rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid var(--rojo-claro);
}

.formulario .campo {
  display: grid;
  margin-bottom: 2rem;

}

.formulario label {
  color: var(--gris-clarito);
  font-size: 2.8rem;
  text-align: center;
  margin-bottom: 2rem;
}

.formulario input[type="textarea"],
.formulario input[type="text"],
.formulario input[type="number"] {
  background-color: var(--beig-clarito);
  border-radius: 1rem;
  padding: 1.2rem;
  border: none;
  flex: 1;
  font-size: 1.7rem;
}

.formulario textarea {
  background-color: var(--beig-clarito);
  border-radius: 1rem;
  padding: 1.2rem;
  border: none;
  flex: 1;
  font-size: 1.7rem;
}

button[type="submit"] {
  background-color: var(--azul-marino);
  border: none;
  padding: 1rem;
  text-align: center;
  margin-top: 2rem;
  color: var(--beig-clarito);
  font-weight: 900;
  text-transform: uppercase;
  font-size: 1.9rem;
  width: 100%;
  transition: background-color 300ms ease;
  transition: 300ms ease;
}

@media (min-width: 768px) {
  button[type="submit"] {
    display: block;
    padding: 1rem 5rem;
  }
}

button[type="submit"]:hover {
  background-color: var(--rojo-claro);
  cursor: pointer;
  transform: scale(1.1);
  color: var(--azul-marino);
}

.formulario select {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  text-align: center;
  font-size: 2rem;
  color: var(--gris-clarito);
  background-color: var(--beig-clarito);
}

.formulario select option {
  background-color: var(--beig-clarito);
}

.alerta {
  padding: 2rem 5rem;
  max-width: 60rem;
  margin: 2rem auto 2rem auto;
  font-weight: 900;
  text-transform: uppercase;
  font-size: 1.8rem;
  text-align: center;
}

.alerta.error {
  background-color: var(--blanco);
  border-left: 5px solid #b91c1c;
  color: #b91c1c;
}

.contenedor {
  width: 90%;
  max-width: 80rem;
  margin: 0 auto;

}

.contenedor-presupuesto {
  margin-top: -5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5rem;
  background-color: var(--beig-gris);
}

.dos-columnas {
  flex-direction: column;
}

.dos-columnas :first-child {
  margin-bottom: 4rem;
}

@media (min-width: 768px) {
  .dos-columnas {
    /* flex-direction: row; */
    align-items: center;
    gap: 4rem;
  }

  .dos-columnas>div {
    width: 50%;
  }

  .dos-columnas :first-child {
    margin-bottom: 0;
  }
}

.formulario input[type="text"].nuevo-presupuesto,
.formulario input[type="number"].nuevo-presupuesto {
  font-size: 2.8rem;
  text-align: center;
  color: var(--gris-clarito);
  background-color: var(--beig-clarito);
}

.contenedor-presupuesto svg {
  width: 25rem;
}

.contenido-presupuesto {
  width: 100%;
}

.contenido-presupuesto p {
  font-weight: 400;
  color: var(--gris-clarito);
  font-size: 2.4rem;
  text-align: center;
}

@media (min-width: 768px) {
  .contenido-presupuesto p {
    text-align: left;
  }
}

.contenido-presupuesto p.negativo,
.contenido-presupuesto p.negativo span {
  color: #DC2626;
}

.contenido-presupuesto span {
  font-weight: 900;
  color: var(--azul-marino);
}

.reset-app {
  border: none;
  background-color: var(--azul-marino);
  padding: 1rem;
  width: 100%;
  text-align: center;
  color: var(--rojo-claro);
  text-transform: uppercase;
  font-weight: 900;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 300ms;
  margin-top: 20px;
  transition: 300ms;
}

.reset-app:hover {
  background-color: var(--rojo-claro);
  cursor: pointer;
  transform: scale(1.2);
  color: var(--azul-marino);
}

.delete-button {
  border: none;
  background-color: var(--rojo-claro);
  padding: 1rem;
  width: 90px;
  text-align: center;
  color: var(--azul-marino);
  text-transform: uppercase;
  font-weight: 900;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 300ms;
}

.delete-button:hover {
  background-color: var(--azul-marino);
  cursor: pointer;
  color: var(--rojo-claro);
}

.edit-button {
  border: none;
  background-color: var(--beig-clarito);
  padding: 1rem;
  width: 90px;
  text-align: center;
  color: var(--azul-marino);
  text-transform: uppercase;
  font-weight: 900;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 300ms;
}

.edit-button:hover {
  background-color: var(--azul-marino);
  cursor: pointer;
  color: var(--beig-clarito);
}

.gastosMain {
  padding-top: 10rem;
}

.filtros .campo {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.filtros label {
  font-size: 3rem;
  font-weight: 900;
  color: var(--azul-marino);
}

.filtros select {
  flex: 1;
  padding: 1rem;
  font-size: 2rem;
  border: none;
  border-radius: 1rem;
  text-align: center;
  background-color: var(--beig-clarito);
}

.filtros select option {
  background-color: var(--beig-clarito)
}

.listado-gastos {
  margin-top: 5rem;
}

.listado-gastos h2 {
  color: var(--azul-marino);
  font-weight: 900;
}

.no-gastos {
  text-align: center;
  font-size: 2.2rem;
}

.gasto {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
  width: 100%;
}

.cantidad-gasto {
  font-size: 2.4rem;
  color: var(--rojo-claro);
  font-weight: 900;
}

.contenido-gasto {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.contenido-gasto img {
  width: 10rem;
}

.descripcion-gasto p {
  margin: 0 0 0.3rem 0;
}

.descripcion-gasto .categoria {
  color: var(--azul-marino);
  font-size: 1.6rem;
  font-weight: 900;
  text-transform: uppercase;
}

.descripcion-gasto p {
  margin-bottom: 1rem;
}

.descripcion-gasto .nombre-gasto {
  color: var(--gris-oscuro);
  font-size: 2.4rem;
  font-weight: 700;
}

.descripcion-gasto .nombre-gasto:hover {
  cursor: pointer;
}

.descripcion-gasto .fecha-gasto {
  font-size: 1.8rem;
  font-weight: 900;
  color: var(--gris-oscuro);
}

.descripcion-gasto .fecha-gasto span {
  font-weight: 400;
}

.nuevo-gasto {
  position: fixed;
  bottom: 5rem;
  right: 5rem;
}

.nuevo-gasto img {
  width: 5rem;
  transition: 700ms;
}

.nuevo-gasto img:hover {
  cursor: pointer;
  transform: scale(2) rotate(180deg);
}

.modal {
  position: fixed;
  background-color: var(--beig-clarito);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.modal .cerrar-modal {
  position: absolute;
  right: 3rem;
  top: 3rem;
  width: 3rem;
  height: 3rem;
}

.myButton2 {
  background-color: transparent;
  margin-right: 100px;
  cursor: pointer;
  color: var(--azul-marino);
  font-family: Arial;
  font-size: 4rem;
  font-weight: bold;
  text-decoration: none;
  transition: 300ms ease-in-out;
}

.myButton2:hover {
  background-color: transparent;
  color: var(--rojo-claro);
}





.modal .formulario {
  max-width: 80rem;
  width: 60rem;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in;
  background-color: var(--beig-fuerte);
  padding: 50px;
  margin-top: 20px;
}

.modal label {
  color: var(--azul-marino);
  text-decoration: var(--azul-marino);
}

body .modal {
  overflow: hidden;
}

.modal .formulario.animar {
  position: relative;
  opacity: 1;
}

.modal .formulario.cerrar {
  opacity: 0;
}

.modal label {
  text-align: left;
}

.CircularProgressbar .CircularProgressbar-text {
  font-size: 1rem !important;
}

/*** SWIPE LIST OVERRIDES **/
.swipe-action__leading {
  background-color: var(--azul);
  color: var(--blanco);
  margin-bottom: 2rem;
}

.swipe-action__trailing {
  background-color: #db2777;
  margin-bottom: 2rem;
  color: var(--blanco);
}

.swipeable-list .swipe-action {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.4rem;
  text-align: right;
}


/* Css para el ToDoList */
.logoHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0px;
  width: 100%;
  background: rgb(249, 245, 235);
  background: linear-gradient(180deg, rgba(249, 245, 235, 1) 83%, rgba(52, 58, 64, 0) 100%);

}

.navBarImage {
  height: 100px;
  width: 100px;
  margin: 30px;
  transition: 300ms;
}

.navBarImage:hover {
  height: 120px;
  width: 120px;
}

.linksHeader {
  display: flex;
  font-weight: 600;
  font-size: x-large;

}

.presupuestoLink {
  margin-right: 30px;
  text-decoration: none;
  color: var(--azul-marino);
  transition: 400ms;
}

.presupuestoLink:hover {
  font-size: xx-large;
  color: var(--rojo-claro);
}

.todolistLink {
  margin-right: 100px;
  text-decoration: none;
  color: var(--azul-marino);
  transition: 400ms;
}

.todolistLink:hover {
  font-size: xx-large;
  color: var(--rojo-claro);
}

.myButton {
  background-color: #002a5b;
  border-radius: 50%;
  align-items: center;
  display: inline-block;
  cursor: pointer;
  color: #e4dccf;
  font-family: Verdana;
  font-size: 30px;
  font-weight: bold;
  padding: 15px 20px;
  text-decoration: none;
  transition: 700ms ease-in-out;
}

.myButton:hover {
  background-color: var(--rojo-claro);
  transform: scale(1.2) rotate(180deg);
}

.myButton:active {
  position: relative;
  top: 1px;
}

.positive {
  color: green !important;
}

.negative {
  color: red !important;
}

.noTareas {
  text-align: center;
  margin: auto;
  color: var(--azul-marino);
  margin-top: 5rem;
  font-weight: 900;
  max-width: 300px;
}


/* Scrollbar */

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--beig-clarito);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}